.App {
  display: -ms-flexbox;
  display: flex;
  font-size: 14px;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-header {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: row;
      flex-direction: row;
  background-color: rgb(156, 49, 49);
  padding: 10px; 
  margin: 0;
  color: white;
}

.App-title {
  font-size: 2em;
}

.App-button-add {
  border: none;
  font-size: 1.2em;
  margin-left: 30px;
  width: 15%;
  height: 3em;
}

.App-button-add:hover {
  -webkit-transition: .5s background-color;
  -o-transition: .5s background-color;
  transition: .5s background-color;
  background-color: orange;
}

